	onLoad() {
		uni.setNavigationBarTitle({
			title: "Profile"
		});

		var userInfo = this.getGlobalUserInfo();
		if (userInfo == null || userInfo == undefined || userInfo == "") {
			uni.navigateTo({
				url: '../login/login',
			});
			return;
		}
		// console.log(userInfo)

		uni.showLoading({
			title: "Loading..."
		});
		var that = this;
		uni.request({
			url: that.$serverUrl + '/user/query?userId=' + userInfo.id,
			method: 'POST',
			header: {
				'content-type': 'application/json',
				'userId': userInfo.id,
				'userToken': userInfo.userToken,
			},
			success: (res) => {
				console.log(res.data);
				var status = res.data.status;
				if (status == 200) {
					uni.hideLoading();
				} else if (status == 502) {
					uni.showToast({
						icon: 'none',
						title: res.data.msg,
					});
				}
			}
		});

	},
	methods: {
		changeFaceImage: function() {
			var userInfo = this.getGlobalUserInfo();
			if (userInfo == null || userInfo == undefined || userInfo == "") {
				uni.navigateTo({
					url: '../login/login',
				});
				return;
			}

			var that = this;
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: function(res) {
					var tempFilePaths = res.tempFilePaths;
					uni.showLoading({
						title: 'Uploading...'
					})

					console.log("uploading face... userId=" + userInfo.id);
					uni.uploadFile({
						url: that.$serverUrl + '/user/uploadFace?userId=' + userInfo.id,
						filePath: tempFilePaths[0],
						name: 'file',
						header: {
							'content-type': 'application/json',
							'userId': userInfo.id,
							'userToken': userInfo.userToken,
						},
						success: (res) => {
							var data = JSON.parse(res.data);
							console.log("upload_res=");
							console.log(data);

							uni.hideLoading();
							if (data.status == 200) {
								uni.showToast({
									title: 'Success!',
									icon: "success"
								})
								var imageUrl = data.data;
								that.faceUrl = that.$serverUrl + imageUrl;
							} else if (data.status == 502) {
								uni.showToast({
									title: data.msg
								})
							}
						}
					});
				}
			});
		},

		goToLogin: function() {
			uni.navigateTo({
				url: '../login/login',
			})
		},

		logout: function() {
			var userInfo = this.getGlobalUserInfo();
			if (userInfo == null || userInfo == undefined || userInfo == "") {
				uni.navigateTo({
					url: '../login/login',
				});
				return;
			}

			var that = this;
			uni.request({
				url: that.$serverUrl + '/logout?userId' + userInfo.id,
				method: 'POST',
				header: {
					'content-type': 'application/json'
				},
				success: (res) => {
					console.log(res.data);
					var status = res.data.status;
					if (status == 200) {
						uni.showToast({
							icon: 'none',
							title: 'Logout'
						});
						that.removeGlobalUserInfo();
						console.log("logout: userInfo.id=" + that.getGlobalUserInfo.id);
						console.log("logout: userInfo.userToken=" + that.getGlobalUserInfo.userToken);
						uni.navigateTo({
							url: '../login/login',
						});
					} else if (status == 500) {
						uni.showToast({
							icon: 'none',
							title: res.data.msg,
						});
					}
				},
			});
		}
	}
}
